.main_section {
  /*background-color: aqua;*/
  position: absolute;
  left: 50%;
  top: 0;
  transform: translate(-50%, 0%);

  max-width: 700px;  
  width: 100%;
  height: 90%;
  z-index: 1;

  display: grid;
  grid-template-rows: 250px auto 250px; /* main content, btns , footer */
  position: absolute;
  /*overflow-y: auto;*/
}

.main_overlay-container {
  position: fixed;
  left: 50%;
  bottom: 50%;
  transform: translate(-50%, 50%);
  z-index: 2;
  width: 500px;
  height: 500px;
  max-width: 60%;
}
